import React, { Component } from "react";
import "../components/sidebar-top.css";

class TopSidebar extends Component {
  state = {
    details: [
      {
        id: 1,
        name: "Naresh Jangra",
        from: "IOI Airpot Terminai 3 Metro",
        to: "Hisar",
        imgUrl: "http://www.entrepreneurindia.tv/images/Expert/user.jpg"
      },
      {
        id: 2,
        name: "Nitin pokhriyal",
        from: "IOI Airpot Terminai 3 Metro",
        to: "Delhi",
        imgUrl: "http://www.entrepreneurindia.tv/images/Expert/user.jpg"
      },
      {
        id: 3,
        name: "Ashish pokhriyal",
        from: "IOI Airpot Terminai 3 Metro",
        to: "Dehradun",
        imgUrl: "http://www.entrepreneurindia.tv/images/Expert/user.jpg"
      },
      {
        id: 4,
        name: "Vikas Rana",
        from: "IOI Airpot Terminai 3 Metro",
        to: "Pune",
        imgUrl: "http://www.entrepreneurindia.tv/images/Expert/user.jpg"
      }
    ]
  };

  render() {
    // let fiteredContacts = this.state.details;
    return (
      <React.Fragment>
        {this.state.details.map((person, index) => (
          <div className="card">
            <div className="row">
              <div className="col-3">
                <img className="sidebar-thumbnail" src={person.imgUrl} />
              </div>
              <div className="col-9">
                <div className="card-body">
                  <div className="row">
                    <div className="col-12">
                      <h6 className="card-title" key={index}>
                        {person.name}
                      </h6>
                    </div>
                    <div className="col-4" style={{ fontSize: 10 }}>
                      From:
                    </div>
                    <div className="col-8" style={{ fontSize: 10 }}>
                      {person.from}
                    </div>
                    <div className="col-4" style={{ fontSize: 10 }}>
                      To:
                    </div>
                    <div className="col-8" style={{ fontSize: 10 }}>
                      {person.to}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </React.Fragment>
    );
  }
}

export default TopSidebar;
