import React, { Component } from "react";
import "./sidebar-bottom.css";
class BottomSidebar extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <nav>
          <div className="nav nav-tabs" id="nav-tab" role="tablist">
            <a
              className="nav-item nav-link active"
              id="nav-corporate-tab"
              data-toggle="tab"
              href="#nav-corporate"
              role="tab"
              aria-controls="nav-corporate"
              aria-selected="true"
            >
              Corporate
            </a>
            <a
              className="nav-item nav-link"
              id="nav-group-tab"
              data-toggle="tab"
              href="#nav-group"
              role="tab"
              aria-controls="nav-group"
              aria-selected="false"
            >
              Group
            </a>
          </div>
        </nav>
        <div className="tab-content" id="nav-tabContent">
          <div
            className="tab-pane fade show active"
            id="nav-corporate"
            role="tabpanel"
            aria-labelledby="nav-corporate-tab"
          >
            <p>
              Provide your official mail id to join your company's carpooling
              group
            </p>
            <span className="knowmore">Know More</span>
            <br />
            <form>
              <div className="form-group">
                <input
                  type="email"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter Mail Id"
                />
                <button type="submit" className="btn btn-primary btn-block">
                  VERIFY
                </button>
              </div>
            </form>
          </div>
          <div
            className="tab-pane fade"
            id="nav-group"
            role="tabpanel"
            aria-labelledby="nav-group-tab"
          >
            <p>
              Provide your official mail id to join your company's carpooling
              group
            </p>
            <a className="knowmore">Know More</a>
            <br />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default BottomSidebar;
