import React, { Component } from "react";
import "./map.css";
class MainSection extends Component {
  render() {
    return (
      <React.Fragment>
        <form>
          <div className="form-row">
            <div className="col-6">
              <label className="placeName" htmlFor="placeName">
                Leaving From
              </label>
              <input
                id="placeName"
                type="text"
                className="form-control"
                placeholder="Enter place name"
              />
            </div>
            <div className="col-6">
              <label className="placeName" htmlFor="placeName">
                Going To
              </label>
              <input
                id="placeName"
                type="text"
                className="form-control"
                placeholder="Enter place name"
              />
            </div>
          </div>
        </form>
        <br />
        <div className="map shadow-sm bg-white rounded">
          <img
            src="https://www.harvard.edu/sites/default/files/content/harvard-map-google.jpg"
            alt=""
            className="mapImage"
          />
          <div className="card" id="card">
            <div className="card-body">
              <img
                src="http://www.entrepreneurindia.tv/images/Expert/user.jpg"
                id="thumbnail"
                alt=""
              />
              <h5 className="card-title">Neha Sharma's Carpool</h5>
            </div>
          </div>
        </div>
        <div className=" viewDetails">
          <button type="button" className="btn btn-link">
            View Details
          </button>
        </div>
      </React.Fragment>
    );
  }
}

export default MainSection;
