import React, { Component } from "react";
import "./filter.css";
class Filter extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="filter mb-4" style={{ textAlign: "center" }}>
          <div className="btn-group" role="group" aria-label="Basic example">
            <button type="button" className="groupBtn active">
              All
            </button>
            <button type="button" className="groupBtn">
              Drivers
            </button>
            <button type="button" className="groupBtn">
              Passenger
            </button>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Filter;
